@import url(https://fonts.googleapis.com/css2?family=Modak&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {   
  min-height: 100vh;  
  display: flex;
  flex-direction:  column;
  justify-content: space-between;
  align-items: center;  
  background: url(/Game-Interface/static/media/8766.9ba16977.jpg);
  background-size: cover;
}









.carousel__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}

.carousel{    
    width: 800px;
    height: 320px;       
    display: flex; 
    justify-content:  space-evenly;   
    align-items: center;
    overflow: hidden;        
}


.carousel__message{
    font-family: 'Modak', cursive;    
    font-size: 40px;
    margin: 0;
    color: palevioletred; 
}

.carousel__btn{ 
    all: unset;   
    width: 80px;
    height: 80px;          
    border-radius: 80px;    
    display: flex;
    place-content: center;   
    background-color: white;
    outline: none;
    z-index: 1;
    cursor: pointer;
    box-shadow: inset 2px -3px 11px rgba(80, 92, 51, 0.17);
}

.carousel__btn:hover {
    transform: translateY(-3px);   
    box-shadow: inset 2px -7px 11px rgba(80, 92, 51, 0.17);
}

.carousel__btn:active {
    transform: translateY(-1px);    
    box-shadow: inset 2px -7px 11px rgba(80, 92, 51, 0.17);
}

.carousel__icon{
    font-size: 50px;
    font-weight: bold;
    color: hotpink;          
}

/* Media Queries */

@media(max-width: 1330px){
    .carousel{
        width: 700px;
        height: 300px;       
    }
}

@media(max-width: 820px){
    .carousel{
        width: 500px;
        height: 300px;       
    }

    .carousel__btn{          
        width: 60px;
        height: 60px;
        border-radius: 60px;
    }

    .carousel__icon{
        font-size: 38px;                  
    }
}
.card{ 
    max-width: 100%;    
    margin: 10px 0;    
}

.card img{    
    width: 100%;
    height: 100%;    
    display: block;
    cursor: pointer;      
}

.card__box .card__content {
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: pink;
}

.card__box {
	width: 300px;
	height: 300px;
	box-sizing: border-box;
	padding: 15px;
	position: relative;
	overflow: hidden;
}

.card__box::before {
	content: '';
	position: absolute;
	width: 150%;
	height: 150%;
	background: repeating-linear-gradient(
			white 0%,
			white 7.5px,
			hotpink 7.5px,
			hotpink 15px,
			white 15px,
			white 22.5px,
			hotpink 22.5px,
			hotpink 30px);
	transform: translateX(-20%) translateY(-20%) rotate(-45deg);	
}

.card__box .card__content {
	position: relative;
	background-color: white;
	flex-direction: column;
	box-sizing: border-box;
	padding: 30px;
	text-align: center;
	font-family: sans-serif;
	z-index: 2;
}

.card__box,
.card__box .card__content {
	box-shadow: 0 0 2px deeppink,
				0 0 5px rgba(0, 0, 0, 1),
				inset 0 0 5px rgba(0, 0, 0, 1);
	border-radius: 10px;
}


/* Media Queries */

@media(max-width: 1330px){
    .card__box{
        width: 280px;
        height: 280px;       
    }   
}

@media(max-width: 820px){
    .card__box{
        width: 250px;
        height: 250px;       
    }   
}







.boxes{    
    width: 900px;
    height: 350px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border: 1px solid lightpink;    
    border-radius: 10px;
    margin-top: 10px;
}

.boxes__message{
    font-family: 'Modak', cursive;    
    font-size: 50px;
    color: skyblue;
    margin: 0 !important;       
}

.question-icon{
    font-size: 10rem;
    color:darksalmon;
    position: absolute;
    z-index: 2;    
}


/* Media Queries */

@media(max-width: 1330px){
    .boxes{
        width: 800px;
        height: 320px;
    }

    .boxes__message{           
        font-size: 50px;           
    }
}

@media(max-width: 820px){
    .boxes{
        width: 700px;
        height: 300px;        
    }

    .boxes__message{           
        font-size: 40px;           
    }
} 
.box{
    width:30%;
    height:90%;
    background-color: white;
    border-radius: 10px;
    box-shadow: inset 2px -3px 11px rgba(80, 92, 51, 0.17);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;    
}


.character{
    display: inline-block;
    width: 250px;
    height: 250px;    
    cursor: pointer;
    position: relative;
    z-index: 1;         
}

.character img{    
    width: 100%;
    height: 100%;    
    display: block;      
}

.character button{
    all: unset;
    position: absolute;
    right: 0;
    top: 0;
    width: 25px;
    height: 25px;
    border-radius: 25px;    
    background-color: pink;
    color: white;
    display: flex;    
    justify-content: center;
    align-items: center;        
}


/* Media Queries */

@media(max-width: 1330px){
    .character{
        width: 180px;
        height: 180px
    }

    .character button{
        width: 20px;
        height: 20px;
        border-radius: 20px; 
    }

    .character button span{
        font-size: 12px;
    }
}



.modal__overlay{
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: 2;
}

.modal{
    position: fixed;
    width: 60vw;
    height: 600px;
    display: flex;
    place-items: center;   
    background-color: mistyrose;
    padding: 20px;
    z-index: 3;
    top: 15%;
    left: 15%;
    transform: translate((-50%,-50%));
    border-radius: 10px;
    box-shadow: inset 2px -3px 11px rgba(80, 92, 51, 0.8);
}

.modal__btn{
    all: unset;
    position: absolute;
    right: 2%;
    top: 3%;
    width: 25px;
    height: 25px;
    border-radius: 25px;    
    background-color: rosybrown;
    color: white;
    display: flex;    
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.modal__content{
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 20px;
    width: 100%;
    height: 90%;           
    margin-top: 20px;
    padding: 0 20px;
    border-radius: 10px;
    background-color: ivory;    
}


/* Media Queries */

@media(max-width:1250px){
    .modal{
        width: 95vh;
    }
}

@media(max-width:1250px){
    .modal{
        width: 90vh;
    }
}

@media(max-width: 1000px){
    .modal{        
        left: 20%;
        width: 60vh;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .modal__content{
        display: flex;
        flex-direction: column;
        align-items: center; 
        margin-top: 40px;       
        padding: 20px;
        width: 90%;
        height: auto;
    }
}


.items{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    width: 100%;
    height: 90%;
    background-color: linen;   
    place-self: center;
}

.item__image{
    max-width: 90%; 
    margin: auto;     
}

.add-icon{
    font-size: 5rem;
    color: lightgray;
    max-width: 100%;
    margin: auto;   
}

.itemDisplay__message{
    font-family: 'Modak', cursive;    
    font-size: 40px;
    color: salmon;
    text-align: center; 
}



/* Media Queries */

@media(max-width: 1000px){
    .items{
        grid-gap: 0;
    }
}
.item{
    width: 120px;
    height: 120px;
    border-radius: 10px;
    background-color: white;
    align-self: center;
    justify-self: center;
    display: flex;
    cursor: pointer;  
}


/* Media Queries */

@media(max-width: 1330px){
    .item{
        width: 100px;
        height: 100px;
    }   
}

@media(max-width: 1000px){
    .item{
        width: 80px;
        height: 80px;
        margin: 10px;
    }   
}


.itemDisplay{
    width: 100%;
    height: 90%;
    background-color: linen;
    place-self: center;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;    
}

.itemDisplay__top{
    width: 230px;
    height: 230px;
    border-radius: 10px;
    background-color: white;    
    display: flex;      
}

.itemDisplay__img{
    max-width: 100%;
    place-content: center;
}

.itemDisplay__bottom{
    width: 230px;
    height: 200px;
    border-radius: 10px;   
    background-color: white;    
    display: flex; 
    justify-content: center;
    align-items: center;    
}


/* Media Queries */

@media(max-width: 1400px){
    .itemDisplay__top{
        width: 210px;
        height: 220px;
    } 

    .itemDisplay__bottom{
        width: 210px;
        height: 200px;
    }   
}

@media(max-width: 1000px){
    .itemDisplay{        
        flex-direction: row;
        justify-content: space-evenly; 
        align-items: center;
        padding: 10px 0;       
    }
    
    .itemDisplay__top,
    .itemDisplay__bottom{
        width: 180px;
        height: 180px;
        margin: 0;
    } 
}

.itemProps{    
    width: 95%;
    height: 95%;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    overflow: hidden;           
}

.itemProps > *{    
    width: 230px;
    height: 50px;
    border-radius: 10px;    
    place-self: center;    
}

.itemProps__blue{
    background-color: blue;          
}

.itemProps__red{    
    background-color: red;    
}

.itemProps__yellow{    
    background-color: yellow;    
}


/* Media Queries */

@media(max-width: 1400px){
    .itemProps > *{
        width: 210px;        
    }   
}

@media(max-width: 1100px){
    .itemProps > *{
        width: 180px;        
    }  
}
.toggle__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 110px;
    position: absolute;
    bottom: 5%;
    left: 3%;
}

.toggle__title{
    color: purple;    
    font-weight: bolder;
}

.toggle__btn {
    all: unset;
    background-color: lightyellow;
    position: relative;    
    width: 60px;
    height: 20px;
    display: flex;
    border-radius: 10px;
    align-items: center;
    justify-content: space-around;    
    outline: none;    
}

.toggle__btn span {
    display: inline-block;
    font-size: 15px;
    cursor: pointer;    
    color: rosybrown;
}

.toggle__over {
    position: absolute;
    width: 50%;
    height: 100%;
    top: 0;
    right: 0;
    background: skyblue;
    border-radius: 10px;    
    display: block;
    transition: all 0.3s;
}

.slide .toggle__over {
    right: 50%;
}

.audio__container{
    z-index: -2;
}


@media(max-width: 1100px){
    .toggle__container{        
        top: 0;
        align-items: flex-start;
        margin-top: 20px;        
    }

    .toggle__title{
        margin: 0;
    }
}
